/* ======================================================================== */
/* 6. button */
/* ======================================================================== */
.button {
  position: relative;
  display: inline-block;
  font-size: calc(10 * 1px);
  font-weight: 700;
  letter-spacing: 2px;
  line-height: 1.3;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  transition: all 0.2s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  cursor: pointer;
  outline: none;
  box-shadow: none;
  border-width: 2px;
  border-style: solid;
  padding: 18px 45px;
  border-radius: 64px;
}

@media screen and (min-width: 320px) {
  .button {
    font-size: calc(10 * 1px + (13 - 10) * ((100vw - 320px) / 1600));
  }
}

@media screen and (min-width: 1920px) {
  .button {
    font-size: calc(13 * 1px);
  }
}

.button:focus {
  outline: none;
}

.button[data-hover]:before {
  content: attr(data-hover);
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(100%);
  transition: all 0.2s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  opacity: 0;
  visibility: hidden;
}

.button[data-hover]:hover .button__label-hover {
  transform: translateY(-100%);
  opacity: 0;
  visibility: hidden;
}

.button[data-hover]:hover:before {
  transform: translateY(-50%);
  opacity: 1;
  visibility: visible;
}

.button__label-hover {
  display: block;
  transform: translateY(0%);
  transition: all 0.2s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  opacity: 1;
  visibility: visible;
}

.button_icon {
  display: inline-flex;
  padding: 0;
  border: none;
}

.button__label {
  display: inline-block;
  padding: 18px 45px;
}

.button__icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  transition: all 0.2s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.button__icon i {
  font-size: 24px;
}

.button_fullwidth {
  width: 100%;
}

.button-fullwidth .button {
  width: 100%;
}

@media screen and (max-width: 1680px) {
  .button {
    padding: 14px 32px;
  }
  .button__label {
    padding: 18px 32px;
  }
}

@media screen and (max-width: 991px) {
  .button {
    padding: 12px 28px;
  }
  .button__label {
    padding: 12px 28px;
  }
  .button__icon {
    width: 43px;
  }
  .button__icon i {
    font-size: 20px;
  }
  .button_icon {
    padding: 0;
  }
}

