  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.widget_archive ul li span {
  font-size: 14px;
  line-height: 1;
}

/* ======================================================================== */
/* 80. widgetCalendar */
/* ======================================================================== */
#wp-calendar {
  width: 100%;
  text-align: center;
}

#wp-calendar thead {
  border-bottom: 1px solid var(--color-dark-1);
}

#wp-calendar caption {
  caption-side: top;
  width: 100%;
  text-align: center;
  padding-top: 0;
  padding-bottom: 10px;
}

#wp-calendar th {
  font-weight: 600;
  padding: 5px;
  text-align: center;
}

#wp-calendar td {
  padding: 5px;
}

#wp-calendar td#next {
  text-align: right;
}

#wp-calendar td#prev {
  text-align: left;
}

#wp-calendar tbody a:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  transform: translate(-50%, -50%);
  z-index: -1;
  transition: all 0.2s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

#wp-calendar tr {
  border-bottom: none;
}

#wp-calendar a {
  display: inline-block;
  position: relative;
  z-index: 50;
}

.wp-calendar-nav {
  display: flex;
  justify-content: space-between;
  align-content: center;
  width: 100%;
}

/* ======================================================================== */
/* 81. widgetCategories */
/* ======================================================================== */
.widget_categories ul li {
  display: flex !important;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.widget_categories ul li span {
  font-size: 14px;
  line-height: 1;
}

/* ======================================================================== */
/* 82. widgetMenuInline */
/* ======================================================================== */
.widget_rhye_menu_inline ul li {
  display: inline-block;
  margin-bottom: 0;
}

.widget_rhye_menu_inline ul li a {
  padding: 4px;
}

.widget_rhye_menu_inline ul li a:before {
  display: none;
}

@media screen and (max-width: 991px) {
  .widget_rhye_menu_inline .menu {
    text-align: center;
  }
}

/* ======================================================================== */
/* 83. widgetNavMenu */
/* ======================================================================== */
.widget_nav_menu ul.menu > li {
  white-space: normal;
  word-break: break-word;
}

.widget_nav_menu ul.menu > li a {
  display: inline-block;
  padding: 0;
  font-size: 16px;
  line-height: 1.5;
  font-weight: normal;
  text-transform: none;
  letter-spacing: 0;
  color: var(--color-gray-1);
}

.widget_nav_menu ul.menu > li a:after, .widget_nav_menu ul.menu > li a:before {
  display: none;
}

.widget_nav_menu ul.menu > li a:hover {
  color: var(--color-dark-1);
}

.widget_nav_menu ul.menu > li.menu-item-has-children {
  margin-bottom: 1.5em !important;
}

.widget_nav_menu ul.menu > li.menu-item-has-children a:after {
  display: none;
}

.widget_nav_menu ul.sub-menu {
  background-color: transparent;
  position: relative;
  top: auto;
  left: auto;
  right: auto;
  opacity: 1;
  visibility: visible;
  box-shadow: none;
  border-bottom: none;
  transform: none;
  margin-top: 1em;
  z-index: unset;
}

.widget_nav_menu ul.sub-menu > li {
  border-left: none;
}

.widget_nav_menu ul.sub-menu > li > a {
  padding: 3px 0;
  border-left: none;
}

.widget_nav_menu ul.sub-menu > li > a:hover {
  background-color: unset;
}

/* ======================================================================== */
/* 84. widgetPolylang */
/* ======================================================================== */
.widget_polylang {
  display: inline-block;
  font-size: 14px;
}

.widget_polylang select {
  width: auto;
  display: inline-block;
  padding: 5px 25px 5px 15px;
}

.widget_polylang ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.widget_polylang ul li {
  display: inline-block;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 0;
}

/* ======================================================================== */
/* 85. widgetRSS */
/* ======================================================================== */
.widget_rss .rsswidget {
  font-weight: bold;
}

.widget_rss .rss-date {
  display: block;
  width: 100%;
  font-size: 13px;
  margin-top: 0.5em;
  margin-bottom: 1em;
}

.widget_rss .rssSummary {
  margin-top: 1em;
  margin-bottom: 1em;
}

.widget_rss ul > li {
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
  padding-bottom: 1em;
}

.widget_rss ul > li:last-child {
  border-bottom: none;
  padding-bottom: 0;
}

/* ======================================================================== */
/* 86. widgetRecentComments */
/* ======================================================================== */
.widget_recent_comments ul li {
  padding: 20px 0;
  margin-bottom: 0 !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
}

.widget_recent_comments ul li:first-child {
  padding-top: 0;
}

.widget_recent_comments ul li:last-child {
  padding-bottom: 0;
  border-bottom: none;
}

.widget_recent_comments ul li a {
  font-weight: bold;
}

.widget_recent_comments .comment-author-link a {
  font-family: inherit;
  font-weight: normal;
}

/* ======================================================================== */
/* 87. widgetRecentEntries */
/* ======================================================================== */
.widget_recent_entries .post-date {
  display: block;
  width: 100%;
  font-size: 13px;
  margin-top: 0.25em;
  color: var(--color-gray-1);
}

.widget_recent_entries ul li {
  padding: 20px 0;
  margin-bottom: 0 !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
}

.widget_recent_entries ul li a {
  font-weight: bold;
  line-height: 1.5;
}

.widget_recent_entries ul li a span {
  display: inline-block;
}

.widget_recent_entries ul li:first-child {
  padding-top: 0;
}

.widget_recent_entries ul li:last-child {
  padding-bottom: 0;
  border-bottom: none;
}

/* ======================================================================== */
/* 88. widgetSocial */
/* ======================================================================== */
.widget_rhye_social ul {
  text-align: inherit;
}

.widget_rhye_social ul li {
  display: inline-block;
  margin-bottom: 0;
}

