/* ======================================================================== */
/* 52. section */
/* ======================================================================== */
.section
	position: relative
	backface-visibility: hidden
.section-fullheight
	display: flex
.section-fullheight__inner
	position: relative
	display: flex
	flex-direction: column
	justify-content: center
	width: 100%
	min-height: 100vh
.section-fullheight__inner_mobile
	min-height: calc(var(--fix-bar-vh, 1vh) * 100)
.section_z-100
	z-index: 100
.section__content
	position: relative
	z-index: 60
.section__headline
	display: block
	@include fluid('width', 60, 120)
	height: 1px
	background-color: $brown-grey
.text-left
	.section__headline
		margin-left: 0
		margin-right: auto
.text-center
	.section__headline
		margin-left: auto
		margin-right: auto
.text-right
	.section__headline
		margin-left: auto
		margin-right: 0
.section__bg
	position: absolute
	top: 0
	left: 0
	width: 100%
	height: 100%
	overflow: hidden
.section__circle
	position: absolute
	top: 50%
	left: 50%
	transform: translate(-50%, -50%)
	margin: auto
	width: calc(100vw - var(--gutter-horizontal) * 4)
	height: calc(100vw - var(--gutter-horizontal) * 4)
	z-index: 1
	pointer-events: none

@media screen and (max-width: $md)
	.section-fullheight__inner_mobile-auto
		min-height: 0
		height: 100%
		display: block
