/* ======================================================================== */
/* 28. header */
/* ======================================================================== */
.header
	pointer-events: none
	padding-top: var(--gutter-vertical)
	a, select, input, .lang-switcher
		pointer-events: initial
.header_absolute
	position: absolute
	top: 0
	left: 0
	width: 100%
	z-index: 500
.header_fixed
	position: fixed
	top: 0
	left: 0
	right: 0
	z-index: 500
.header__container
	padding-left: 0
	padding-right: 0
	position: relative
	z-index: 501
.header__col
	opacity: 0
	visibility: hidden
.header__wrapper-burger
	position: absolute
	width: 80px
	height: 80px
.header__burger
	display: inline-block
	position: relative
	width: 40px
	height: 38px
	vertical-align: middle
	cursor: pointer
	pointer-events: initial
	z-index: 500
	&:hover
		.header__burger-line
			&:nth-of-type(1)
				transform: scaleX(1)
			&:nth-of-type(2)
				transform: scaleX(0.75)
			&:nth-of-type(3)
				transform: scaleX(1)
.header__burger_opened, .header__burger_opened:hover
	.header__burger-line
		&:nth-of-type(1)
			transform: scaleX(1) rotate(45deg) translate(13px, 20px)
		&:nth-of-type(2)
			transform: scaleX(0)
		&:nth-of-type(3)
			transform: scaleX(1) rotate(-45deg) translate(14px, -21px)
.header__burger-line
	position: absolute
	width: 100%
	height: 2px
	background-color: var(--color-dark-2)
	+trans1
	&:nth-of-type(1)
		top: 9px
		left: 0
		transform-origin: right center
		transform: scaleX(0.75)
	&:nth-of-type(2)
		top: 0
		left: 0
		bottom: 0
		margin: auto
		transform-origin: left center
	&:nth-of-type(3)
		bottom: 9px
		left: 0
		transform-origin: right center
		transform: scaleX(0.75)
.header__burger_2-lines
	width: 34px
	height: 34px
	.header__burger-line
		&:nth-of-type(1)
			top: 12px
			transform: scaleX(1)
		&:nth-of-type(2)
			display: none
		&:nth-of-type(3)
			bottom: 12px
			transform: scaleX(1)
	&.header__burger_opened, &.header__burger_opened:hover
		.header__burger-line
			&:nth-of-type(1)
				transform: scaleX(1) rotate(45deg) translate(10px, 13px)
			&:nth-of-type(3)
				transform: scaleX(1) rotate(-45deg) translate(11px, -14px)
.header__wrapper-menu
	position: relative
	width: 100%
	margin: auto 0
	padding-top: 3em
	padding-bottom: 3em
.header__wrapper-menu_has-widgets
	padding-top: var(--gutter-horizontal)
	padding-bottom: 3em
.header__wrapper-overlay-menu
	display: flex
	flex-direction: column
	align-items: center
	position: fixed !important
	top: 0
	left: 0
	width: 100vw
	height: calc(var(--fix-bar-vh, 1vh) * 100)
	z-index: 500
	background: none !important
	background-color: unset !important
	overflow-x: hidden
	overflow-y: auto
	-webkit-overflow-scrolling: touch
	opacity: 0
	visibility: hidden
	pointer-events: initial
	text-align: center
	.scroll-content
		margin: auto 0
		width: 100%
.header_menu-right
	.menu
		.sub-menu
			left: auto
			right: 15px
			ul
				left: auto
				right: calc(100% + 1px)
.header__overlay-menu-back
	display: inline-block
	position: absolute
	top: 0
	bottom: 0
	margin: auto 0
	left: -2px
	cursor: pointer
	opacity: 0
	visibility: hidden
	pointer-events: initial
	// content-visibility: auto
.header-curtain
	position: fixed
	top: 0
	left: 0
	width: 100vw
	height: 100vh
	transform: translateY(100%)
	margin: auto
	z-index: 100
	overflow: hidden
.header-curtain_transition
	z-index: 101
.header__wrapper-overlay-widgets
	position: relative
	// margin-top: auto
	padding-top: 4em
	padding-bottom: 3em
	width: 100%
.header__wrapper-overlay-widgets__border
	position: absolute
	top: 0
	left: 20px
	right: 20px
	border-top: 1px solid $borders-dark
	z-index: 50
.header__widget
	font-size: 14px
	p
		font-size: 14px
		line-height: 2
		margin-top: 0
		margin-bottom: 0
.header__col-lang-switcher
	margin-left: auto

@media screen and (max-width: $md)
	.header__wrapper-overlay-menu
		text-align: center
	.header__wrapper-menu
		max-width: 100%
	.header__wrapper-menu_has-widgets
		padding-top: 4em
		padding-bottom: 1em
	.header__wrapper-overlay-widgets
		max-width: 100%
		margin-top: 0
		padding-top: 3em
		padding-bottom: 0
		margin-bottom: 3em
	.header__widget
		&:last-child
			margin-bottom: 1em
	.header__burger
		width: 30px
		height: 35px
	.header__burger_opened, .header__burger_opened:hover
		.header__burger-line
			&:nth-of-type(1)
				transform: scaleX(1) rotate(45deg) translate(10px, 15px)
			&:nth-of-type(2)
				transform: scaleX(0)
			&:nth-of-type(3)
				transform: scaleX(1) rotate(-45deg) translate(11px, -16px)
	.header__burger_2-lines
		&.header__burger_opened, &.header__burger_opened:hover
			.header__burger-line
				&:nth-of-type(1)
					transform: scaleX(1) rotate(45deg) translate(9px,12px)
				&:nth-of-type(3)
					transform: scaleX(1) rotate(-45deg) translate(10px,-13px)
	.header__overlay-menu-back
		top: 2px
