/* ======================================================================== */
/* 46. post */
/* ======================================================================== */
.post:not([data-elementor-type="single-post"])
	max-width: 900px
	width: 100%
.post
	blockquote
		&:before
			content: url('../img/general/quote-black.svg')
			display: block
			width: 2.5em
			height: 2.5em
			float: left
			margin-right: 1em
			margin-top: 0.33em
		cite
			display: block
			text-align: right
			&:before
				content: ''
				width: 60px
				height: 1px
				display: inline-block
				vertical-align: middle
				background-color: var(--color-gray-1)
				margin: 1em
.post-wrapper
	max-width: 1180px
.post__content, .post__comments, .section-content__heading, .section-content__text
	> *:first-child
		margin-top: 0
	> *:last-child
		margin-bottom: 0
	ul
		+reset-ul
		margin-bottom: 1.5em
		li
			display: block
			margin-top: 1em
			margin-bottom: 1em
		ul, ol
			padding-left: 1em
	ul:not(.wp-block-gallery)
		padding-left: 1.5em
		li
			&:before
				content: ''
				display: inline-block
				width: 1em
				height: 2px
				vertical-align: middle
				margin-right: 0.5em
				margin-bottom: 3px
				background-color: var(--color-gray-1)
			> span // split text wrapper
				vertical-align: top !important
	ol:not(.comment-list)
		margin-bottom: 24px
		padding-left: 1.25em
		li
			display: list-item
			margin-top: 0.75em
			margin-bottom: 0.75em
			> span // split text wrapper
				vertical-align: top !important
		ul, ol
			padding-left: 1.25em
.post__content, .comment-content, .section-content__heading, .section-content__text
	> ul
		padding-left: 0 !important
.post__media
	margin-bottom: 2em
.post__tags
	border-top: 2px solid $black
	padding-top: 1.5em

.post__read-more
	padding: 16px 30px
.sticky
	border-radius: 2px
	border: 1px solid var(--color-dark-1)
	padding: 3em

@media screen and (max-width: $sm)
	.sticky
		padding: 2em
	.post-wrapper
		padding-left: 0 !important
		padding-right: 0 !important
