/* 6. button */
/* ======================================================================== */
.button {
	position: relative;
	display: inline-block;
	@include small-caps;
	font-weight: bold;
	text-align: center;
	@include trans1;
	cursor: pointer;
	outline: none;
	box-shadow: none;
	border-width: 2px;
	border-style: solid;
	padding: 18px 45px;
	border-radius: 64px;
	&:focus {
		outline: none; } }

.button[data-hover] {
	&:before {
		content: attr(data-hover);
		display: block;
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		transform: translateY(100%);
		@include trans1;
		opacity: 0;
		visibility: hidden; }
	&:hover {
		.button__label-hover {
			transform: translateY(-100%);
			opacity: 0;
			visibility: hidden; }
		&:before {
			transform: translateY(-50%);
			opacity: 1;
			visibility: visible; } } }

.button__label-hover {
	display: block;
	transform: translateY(0%);
	@include trans1;
	opacity: 1;
	visibility: visible; }

.button_icon {
	display: inline-flex;
	padding: 0;
	border: none; }

.button__label {
	display: inline-block;
	padding: 18px 45px; }

.button__icon {
