	display: inline-block;
	position: relative;
	width: 40px;
	height: 38px;
	vertical-align: middle;
	cursor: pointer;
	pointer-events: initial;
	z-index: 500;
	&:hover {
		.header__burger-line {
			&:nth-of-type(1) {
				transform: scaleX(1); }
			&:nth-of-type(2) {
				transform: scaleX(0.75); }
			&:nth-of-type(3) {
				transform: scaleX(1); } } } }
.header__burger_opened, .header__burger_opened:hover {
	.header__burger-line {
		&:nth-of-type(1) {
			transform: scaleX(1) rotate(45deg) translate(13px, 20px); }
		&:nth-of-type(2) {
			transform: scaleX(0); }
		&:nth-of-type(3) {
			transform: scaleX(1) rotate(-45deg) translate(14px, -21px); } } }
.header__burger-line {
	position: absolute;
	width: 100%;
	height: 2px;
	background-color: var(--color-dark-2);
	@include trans1;
	&:nth-of-type(1) {
		top: 9px;
		left: 0;
		transform-origin: right center;
		transform: scaleX(0.75); }
	&:nth-of-type(2) {
		top: 0;
		left: 0;
		bottom: 0;
		margin: auto;
		transform-origin: left center; }
	&:nth-of-type(3) {
		bottom: 9px;
		left: 0;
