		flex-shrink: 0; }
	.modal-body {
		overflow-y: auto; } }

.modal-dialog-centered {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	align-items: center;
	min-height: calc(100% - 1rem);
	&::before {
		display: block;
		height: calc(100vh - 1rem);
		content: ""; }
	&.modal-dialog-scrollable {
		-ms-flex-direction: column;
		flex-direction: column;
		-ms-flex-pack: center;
		justify-content: center;
		height: 100%;
