/* ======================================================================== */
/* 43. pageIndicator */
/* ======================================================================== */
.page-indicator {
	pointer-events: none;
	position: fixed;
	bottom: 60px;
	right: var(--gutter-horizontal);
	z-index: 500; }

/* ======================================================================== */
/* 44. overlay */
/* ======================================================================== */
.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
	backface-visibility: hidden;
	pointer-events: none; }
.overlay_dark {
	background-color: rgba(0, 0, 0, .6); }
.overlay_dark-30 {
	background-color: rgba(0, 0, 0, .2); }
.overlay_dither {
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		opacity: .2;
		background: url(data:image/png;base64,) repeat; } }
.overlay_circle-dark {
	background-image: radial-gradient(circle at center, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 90%, rgba(0,0,0,1) 100%); }
.overlay_circle-dark-70 {
	background-image: radial-gradient(circle at center, rgba(0, 0, 0, 0) 0%, rgba(0,0,0,.7) 100%); }
.overlay_bottom-dark {
	background-image: linear-gradient(0deg, rgba(17,17,17,1) 0%, rgba(17,17,17,0) 100%); }
.overlay_top-dark {
	background-image: linear-gradient(180deg, rgba(17,17,17,1) 0%, rgba(17,17,17,0) 100%); }
.overlay_light {
	background-color: rgba(255, 255, 255, .8); }

/* ======================================================================== */
/* 45. pagination */
/* ======================================================================== */
.pagination {
	@include fluid('margin-top', 40, 90);
	border-top: 2px solid $black;
	padding: 10px 0 0;
	.nav-links {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	.nav-links__container {
		margin-left: auto;
		margin-right: auto; }
	.page-numbers {
		display: inline-block;
		line-height: 1;
		font-weight: 600;
		padding: 12px 15px;
		background-image: none;
		vertical-align: middle;
		transition-property: color;
		&.prev {
			font-size: 24px;
			font-weight: normal;
			margin-left: -20px; }
		&.next {
			font-size: 24px;
			font-weight: normal;
			margin-right: -20px; }
		&:not(a) {
			color: $brown-grey; }
		&.current {
			color: var(--color-dark-1); } } }
.page-links {
	@include fluid('margin-top', 40, 90);
	border-top: 2px solid $black;
	padding: 10px 0 0;
	.page-number {
		display: inline-block;
		line-height: 1;
		font-weight: 600;
		padding: 0 15px;
		border: none; }
	a {
		background-image: none; }
	.post-page-numbers {
		&:not(a) {
			color: $brown-grey; } } }

.comments-pagination {
	text-align: center;
	.page-numbers {
		display: inline-block;
		line-height: 1;
		font-weight: 600;
		padding: 0 10px;
		transition-property: color; } }

@media screen and (max-width: $md) {
	.pagination {
		padding: 8px 0 0; }
	.page-links {
		padding: 8px 0 0; } }

/* ======================================================================== */
/* 46. post */
/* ======================================================================== */
.post:not([data-elementor-type="single-post"]) {
	max-width: 900px;
	width: 100%; }
.post {
	blockquote {
		&:before {
			content: url('../img/general/quote-black.svg');
			display: block;
			width: 2.5em;
			height: 2.5em;
			float: left;
			margin-right: 1em;
			margin-top: 0.33em; }
		cite {
			display: block;
			text-align: right;
			&:before {
				content: '';
				width: 60px;
				height: 1px;
				display: inline-block;
				vertical-align: middle;
				background-color: var(--color-gray-1);
				margin: 1em; } } } }
.post-wrapper {
	max-width: 1180px; }
.post__content, .post__comments, .section-content__heading, .section-content__text {
	> *:first-child {
		margin-top: 0; }
	> *:last-child {
		margin-bottom: 0; }
	ul {
		@include reset-ul;
		margin-bottom: 1.5em;
		li {
			display: block;
			margin-top: 1em;
			margin-bottom: 1em; }
		ul, ol {
			padding-left: 1em; } }
	ul:not(.wp-block-gallery) {
		padding-left: 1.5em;
		li {
			&:before {
				content: '';
				display: inline-block;
				width: 1em;
				height: 2px;
				vertical-align: middle;
				margin-right: 0.5em;
				margin-bottom: 3px;
				background-color: var(--color-gray-1); }
