		display: inline-block;
		margin-left: 6px;
		margin-right: 5px;
		transition: opacity 0.6s ease; }
	ul {
		padding-left: 0; } }

/* ======================================================================== */
/* 48. postsNavigation */
/* ======================================================================== */
.posts-navigation {
  border-top: 1px solid $borders-dark; }
.posts-navigation__item_prev {
  text-align: left; }
.posts-navigation__item_next {
  text-align: right; }

@media screen and (max-width: $md) {
