/* ======================================================================== */
/* 50. sectionAbout */
/* ======================================================================== */
.section-about {
	max-width: 1680px;
	margin-left: auto;
	margin-right: auto; }
.section-about__offset-container {
	max-width: calc(100vw - var(--gutter-horizontal));
