		.section-blog__wrapper-post {
			padding-top: 40px;
			padding-bottom: 40px; } } }

/* ======================================================================== */
/* 52. section */
/* ======================================================================== */
.section {
	position: relative;
	backface-visibility: hidden; }
.section-fullheight {
	display: flex; }
.section-fullheight__inner {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 100%;
	min-height: 100vh; }
.section-fullheight__inner_mobile {
	min-height: calc(var(--fix-bar-vh, 1vh) * 100); }
.section_z-100 {
	z-index: 100; }
.section__content {
	position: relative;
	z-index: 60; }
.section__headline {
	display: block;
	@include fluid('width', 60, 120);
	height: 1px;
	background-color: $brown-grey; }
.text-left {
	.section__headline {
		margin-left: 0;
		margin-right: auto; } }
.text-center {
	.section__headline {
		margin-left: auto;
		margin-right: auto; } }
.text-right {
	.section__headline {
		margin-left: auto;
		margin-right: 0; } }
.section__bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden; }
.section__circle {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	margin: auto;
	width: calc(100vw - var(--gutter-horizontal) * 4);
	height: calc(100vw - var(--gutter-horizontal) * 4);
	z-index: 1;
	pointer-events: none; }

@media screen and (max-width: $md) {
	.section-fullheight__inner_mobile-auto {
		min-height: 0;
		height: 100%;
		display: block; } }

/* ======================================================================== */
/* 53. sectionHeight */
/* ======================================================================== */
@for $i from 1 through 9 {
	.section_h-#{$i * 100} {
		height: #{$i * 100}px;
		max-height: 100vh; } }
.section_h-100vh {
	height: 100vh; }
.section_w-container-right {
	max-width: 100%;
	width: calc( 100% - ( (100vw - 1140px) / 2 ) - 20px );
	margin-left: auto;
	text-align: left; }
.section_w-container-left {
	max-width: 100%;
	width: calc( 100% - ( (100vw - 1140px) / 2 ) - 20px );
	margin-right: auto;
	text-align: right; }

@media screen and (max-width: $xxl) {
	.section_w-container-right, .section_w-container-left {
		width: 100%;
		text-align: center;
		.section-image__caption-vertical-left, .section-image__caption-vertical-right {
			position: relative;
			top: auto;
			left: auto;
			right: auto;
			bottom: auto;
			transform: none;
			padding: 0 20px;
			margin-top: 1em;
			text-align: center;
			width: 100%; }
		.section-image__caption {
			&.text-left, &.text-center, &.text-right {
				text-align: center !important; } } } }

@media screen and (max-width: $sm) {
	@for $i from 1 through 9 {
		.section_h-#{$i * 100} {
			max-height: 70vh; } } }

/* ======================================================================== */
/* 54. sectionOffset */
/* ======================================================================== */
.section-offset__content {
	position: relative;
	width: 100%; }

/* ======================================================================== */
/* 55. sectionMasthead */
/* ======================================================================== */
.section-masthead__inner {
	position: relative;
	overflow: hidden;
	z-index: 50; }
.section-masthead__background {
	overflow: hidden; }
.section-masthead__background_fullscreen, .section-masthead__background_halfscreen {
	position: absolute !important;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden; }
.section-masthead__background_halfscreen-gutters {
	padding: calc(var(--gutter-horizontal) + 40px) var(--gutter-horizontal) var(--gutter-horizontal); }
.section-masthead__text {
	display: inline-block;
	max-width: 700px; }
.section-masthead__wrapper-scroll-down {
	display: inline-block;
	position: absolute;
	bottom: 2px;
	z-index: 50; }
.text-center .section-masthead__wrapper-scroll-down {
	left: 0;
	right: 0;
	margin: auto; }
.text-left .container-fluid .section-masthead__wrapper-scroll-down {
	left: calc(var(--gutter-horizontal) - 20px); }
.text-right .container-fluid .section-masthead__wrapper-scroll-down {
	right: calc(var(--gutter-horizontal) - 20px); }
.text-left .container .section-masthead__wrapper-scroll-down {
	left: 0; }
.text-right .container .section-masthead__wrapper-scroll-down {
	right: 0; }
.overlay.section-masthead__overlay {
	z-index: 1;
	transform: scale(1.02); } // prevent flicking on scroll
.section-masthead__header {
	z-index: 50; }
.section-masthead__header_absolute {
	position: absolute;
	top: 50%;
	left: 0;
	right: 0;
	transform: translateY(-50%);
	z-index: 50; }
.section-masthead__wrapper-info {
	margin-top: 2em;
	margin-bottom: 2em;
	&:first-child {
		margin-top: 0; }
	&:last-child {
		margin-bottom: 0; } }
.section-masthead__meta-item {
	display: inline-block;
	&:not(:last-child) {
		&:after {
			content: '';
			display: inline-block;
			vertical-align: middle;
			width: 60px;
			@include fluid('width', 40, 60);
			height: 1px;
			margin: 1em;
			background-color: var(--color-gray-1); } }
	> * {
		display: inline-block;
		vertical-align: middle; } }

.section-masthead__meta-categories {
	@include reset-ul; }
.section-masthead__bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1; }
.section-masthead[data-arts-os-animation] {
	.lazy-bg, .lazy-bg-image, .lazy {
		background-color: unset !important; } }
.section-masthead[data-arts-os-animation] .section-masthead__meta .post-meta {
	li:not(:last-child):after {
		opacity: 0; } }
.section-masthead[data-arts-os-animation] .section-masthead__meta.animated .post-meta {
	li:not(:last-child):after {
		opacity: 1;
		transition-delay: 0.4s; } }

@media screen and (max-width: $md) {
	.section-masthead__wrapper-scroll-down {
		bottom: 4px; }
	.section-masthead__background_halfscreen {
		position: relative !important;
		top: auto;
		left: auto;
		right: auto;
		bottom: auto;
		height: calc(var(--fix-bar-vh, 1vh) * 100);
		transform: none;
		&.section-fullheight__inner_mobile {
			height: 100%; } }
	.section-masthead__background_halfscreen-gutters {
		padding: 0; } }

/* ======================================================================== */
/* 56. sectionProperties */
/* ======================================================================== */
.section-properties {
  margin-top: -1em;
  margin-bottom: -1em; }
.section-properties__item {
  margin-top: 1em;
  margin-bottom: 1em; }

@media screen and (max-width: $md) {
  .section-properties_no-margins {
    margin-top: 0;
    margin-bottom: 0; } }

/* ======================================================================== */
/* 57. sectionServices */
/* ======================================================================== */
.section-services__wrapper-letter {
	display: inline-block;
	position: absolute;
	bottom: 66%;
	z-index: 0; }
.section-services__wrapper-letter_right {
	right: 100px; }
.section-services__wrapper-letter_left {
	left: 100px; }
.section-services__letter {
	display: inline-block;
	line-height: 1;
	@include fluid-type(67, 400);
	color: $black;
	opacity: .05; }
.section-services__wrapper-content {
	position: relative; }

@media screen and (max-width: $md) {
	.section-services__container {
		padding-left: 0;
		padding-right: 0;
		>.row {
			margin-left: 0;
			margin-right: 0; } } }

/* ======================================================================== */
/* 58. sectionScroll */
/* ======================================================================== */
.section-scroll {
  transition: background-color 0.4s ease; }

/* ======================================================================== */
/* 59. select */
/* ======================================================================== */
select {
	display: block;
	border: 1px solid $borders-dark;
	border-radius: 2px;
	padding: 8px 35px 8px 15px;
	background-color: transparent;
	outline: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	background-image: url('../img/general/select_black.png');
	background-position: right 15px bottom 50%;
	background-repeat: no-repeat;
	background-size: 6px 4px; }

/* ======================================================================== */
/* 60. social */
/* ======================================================================== */
.social {
	@include reset-ul; }
.social__item {
	display: inline-block;
	transition: none;
	a {
		display: flex !important;
		align-items: center;
		justify-content: center;
		width: 30px;
		height: 30px;
		font-size: 14px; } }

/* ======================================================================== */
/* 61. sidebar */
/* ======================================================================== */
.sidebar_no-margin-last-widget {
	.widget {
		&:last-child {
			margin-bottom: 0; } } }
.sidebar {
	.search-form {
		margin-top: 0; } }

@media only screen and (max-width: $md) {
	.sidebar {
		margin-top: 80px; } }

@media only screen and (max-width: $sm) {
	.sidebar {
		margin-top: 60px; } }

/* ======================================================================== */
/* 62. table */
/* ======================================================================== */
table {
	border-collapse: collapse;
	margin: 0 0 24px; }

thead th {
	border-bottom: 1px solid $dark;
	padding-bottom: 0.5em; }

th {
	padding: 0.75em;
	text-align: left;
	white-space: nowrap; }

tr {
	border-bottom: 1px solid $borders-dark; }

td {
	padding: 0.75em; }

th:first-child, td:first-child {
	padding-left: 0; }

th:last-child, td:last-child {
	padding-right: 0; }

/* ======================================================================== */
/* 63. spinner */
/* ======================================================================== */
$offset: 202;
$duration: 1.2s;

.spinner {
	position: fixed;
	left: var(--gutter-horizontal);
	bottom: calc(var(--fix-bar-vh, 30px) + 15px);
	width: 30px;
	height: 30px;
	z-index: 10000;
	animation: rotator $duration ease-in-out infinite;
	opacity: 0;
	visibility: hidden; }

@keyframes rotator {
	0% {
		transform: rotate(0deg); }
	100% {
		transform: rotate(270deg); } }

.spinner__path {
	stroke: var(--color-gray-1);
	stroke-dasharray: $offset;
	stroke-dashoffset: 0;
	transform-origin: center;
	animation: dash $duration ease-in-out infinite; }

@keyframes dash {
	0% {
		stroke-dashoffset: $offset;
		transform: rotate(0deg); }
	50% {
		stroke-dashoffset: $offset / 4;
		transform: rotate(135deg); }
	100% {
		stroke-dashoffset: $offset;
		transform: rotate(450deg); } }

/* ======================================================================== */
/* 64. svg-rectangle */
/* ======================================================================== */
.svg-rectangle {
	fill: #fff; }
.svg-rectangle__normal {
	visibility: hidden; }

/* ======================================================================== */
/* 65. tags */
/* ======================================================================== */
.tagcloud, .widget .tagcloud {
	a {
		display: inline-block;
		font-size: 12px !important;
		font-weight: 700;
		letter-spacing: 1px;
		line-height: 1;
		text-transform: uppercase;
		background-color: transparent;
		padding: 8px 14px;
		margin-bottom: 6px;
		margin-right: 4px;
		border-radius: 100px;
		border: 1px solid $borders-dark;
		&:hover {
			border-color: $black; } }
	ul {
		@include reset-ul;
		li {
			display: inline-block;
			margin-bottom: 0; } } }

/* ======================================================================== */
/* 66. themes */
/* ======================================================================== */
[data-arts-theme-text="light"]:not([data-arts-header-overlay-theme-text="dark"]), .arts-elementor-theme-light, {
	.section__headline {
		background-color: var(--color-gray-2); }
	color: #fff;
	.xxl {
		color: var(--xxl-color-light); }
	.xl {
		color: var(--xl-color-light); }
	h1, .h1 {
		color: var(--h1-color-light); }
	h2, .h2 {
		color: var(--h2-color-light); }
	h3, .h3 {
		color: var(--h3-color-light); }
	h4, .h4 {
		color: var(--h4-color-light); }
	h5, .h5 {
		color: var(--h5-color-light); }
	h6, .h6 {
