\:root
	/* Fonts */
	--font-primary: 'Raleway', sans-serif
	--font-secondary: 'Cinzel', serif
	/* Colors */
	--color-dark-1: #111111
	--color-dark-2: #262626
	--color-dark-3: #333333
	--color-dark-4: #555555
	--color-light-1: #eeece6
	--color-light-2: #f2f1ed
	--color-light-3: #f7f6f3
	--color-light-4: #f1e9db
	--color-gray-1: #777777
	--color-gray-2: #cccccc
	/* XL heading */
	--xxl-max-font-size: 287
	--xxl-min-font-size: 60
	--xxl-color-dark: #333333
	--xxl-color-light: #eeece6
	/* XL heading */
	--xl-max-font-size: 162
	--xl-min-font-size: 54
	--xl-color-dark: #333333
	--xl-color-light: #eeece6
	/* h1 heading */
	--h1-max-font-size: 88
	--h1-min-font-size: 35
	--h1-color-dark: #333333
	--h1-color-light: #eeece6
	/* h2 heading */
	--h2-max-font-size: 61
	--h2-min-font-size: 31
	--h2-color-dark: #333333
	--h2-color-light: #eeece6
	/* h3 heading */
	--h3-max-font-size: 42
	--h3-min-font-size: 28
	--h3-color-dark: #333333
	--h3-color-light: #eeece6
	/* h4 heading */
	--h4-max-font-size: 26
	--h4-min-font-size: 22
	--h4-color-dark: #262626
	--h4-color-light: #ffffff
	/* h5 heading */
	--h5-max-font-size: 18
	--h5-min-font-size: 18
	--h5-color-dark: #262626
	--h5-color-light: #ffffff
	/* h6 heading */
	--h6-max-font-size: 14
	--h6-min-font-size: 14
	--h6-color-dark: #262626
	--h6-color-light: #ffffff
	/* Subheading */
	--subheading-max-font-size: 13
	--subheading-min-font-size: 10
	--subheading-color-dark: #777777
	--subheading-color-light: #ffffff
	/* Paragraph */
	--paragraph-max-font-size: 16
	--paragraph-min-font-size: 16
	--paragraph-color-dark: #262626
	--paragraph-color-light: #cccccc
	/* Blockquote */
	--blockquote-max-font-size: 24
	--blockquote-min-font-size: 16
	--blockquote-color-dark: #262626
	--blockquote-color-light: #ffffff
	/* Dropcap */
	--dropcap-max-font-size: 94
	--dropcap-min-font-size: 60
	--dropcap-color-dark: #111111
	--dropcap-color-light: #ffffff
	/* Fluid paddings & margins (min values) */
	--distance-min-xsmall: 30
	--distance-min-small: 30
	--distance-min-medium: 40
	--distance-min-large: 100
	--distance-min-xlarge: 160
	/* Fluid paddings & margins (max values) */
	--distance-max-xsmall: 50
	--distance-max-small: 80
	--distance-max-medium: 160
	--distance-max-large: 240
	--distance-max-xlarge: 400
	/* Container & page gutters */
	--gutter-horizontal: 120px
	--gutter-vertical: 80px
	@media screen and (max-width: $xxl)
		--gutter-horizontal: 80px
		--gutter-vertical: 60px
	@media screen and (max-width: $xl)
		--gutter-horizontal: 60px
		--gutter-vertical: 40px
	@media screen and (max-width: $md)
		--gutter-horizontal: 20px
		--gutter-vertical: 20px
	/* preloader circle */
	--preloader-circle-max-size: 960
	--preloader-circle-min-size: 280
