/* ======================================================================== */
/* 69. typography */
/* ======================================================================== */
a, .wp-block-freeform.block-library-rich-text__tinymce a
	+trans1
	text-decoration: none
	color: var(--color-gray-1)
	&:hover
		color: var(--color-dark-2)
		text-decoration: none
h1, h2, h3
	margin-top: 0.5em
	margin-bottom: 0.5em
h4, h5, h6
	margin-top: 0.75em
	margin-bottom: 0.75em
.xxl
	font-family: 'Cinzel', serif
	font-weight: normal
	line-height: 1.00
	@include fluid-type(var(--xxl-min-font-size), var(--xxl-max-font-size))
	color: var(--xxl-color-dark)
.xl
	font-family: 'Cinzel', serif
	font-weight: normal
	line-height: 1.1
	@include fluid-type(var(--xl-min-font-size), var(--xl-max-font-size))
	color: var(--xl-color-dark)
h1, .h1
	font-family: 'Cinzel', serif
	font-weight: normal
	line-height: 1.13
	@include fluid-type(var(--h1-min-font-size), var(--h1-max-font-size))
	color: var(--h1-color-dark)
h2, .h2
	font-family: 'Cinzel', serif
	font-weight: normal
	line-height: 1.31
	@include fluid-type(var(--h2-min-font-size), var(--h2-max-font-size))
	color: var(--h2-color-dark)
h3, .h3
	font-family: 'Cinzel', serif
	font-weight: normal
	line-height: 1.4
	@include fluid-type(var(--h3-min-font-size), var(--h3-max-font-size))
	color: var(--h3-color-dark)
h4, .h4
	font-family: 'Cinzel', serif
	font-weight: bold
	line-height: 1.62
	@include fluid-type(var(--h4-min-font-size), var(--h4-max-font-size))
	color: var(--h4-color-dark)
h5, .h5
	font-family: 'Cinzel', serif
	font-weight: bold
	line-height: 1.6
	@include fluid-type(var(--h5-min-font-size), var(--h5-max-font-size))
	color: var(--h5-color-dark)
h6, .h6
	font-family: 'Cinzel', serif
	font-weight: bold
	line-height: 1.6
	@include fluid-type(var(--h6-min-font-size), var(--h6-max-font-size))
	color: var(--h6-color-dark)
p
	margin-top: 1em
	margin-bottom: 1em
	@include fluid-type(var(--paragraph-min-font-size), var(--paragraph-max-font-size))
	line-height: 1.8
	color: var(--paragraph-color-dark)
.paragraph
	@include fluid-type(var(--paragraph-min-font-size), var(--paragraph-max-font-size))
	line-height: 1.8
	color: var(--paragraph-color-dark)
blockquote, .blockquote
	display: block
	@include fluid-type(var(--blockquote-min-font-size), var(--blockquote-max-font-size))
	line-height: 1.6
	font-style: italic
	margin-top: 2em
	margin-bottom: 2em
	color: var(--blockquote-color-dark)
	p
		margin-top: 0
		@include fluid-type(var(--blockquote-min-font-size), var(--blockquote-max-font-size))
		line-height: 1.6
		color: var(--blockquote-color-dark)
		&:last-child
			margin-bottom: 0
	cite
		@include fluid-type(var(--paragraph-min-font-size), var(--paragraph-max-font-size))
		font-style: normal
		color: var(--blockquote-color-dark)
strong, b, .strong
	font-weight: bold
em, i, .em
	font-style: italic
small, .small
	font-family: 'Raleway', sans-serif
	// display: block
	font-size: 14px
.subheading
	font-family: 'Raleway', sans-serif
	font-weight: 700
	line-height: 1.3
	@include fluid-type(var(--subheading-min-font-size), var(--subheading-max-font-size))
	color: var(--subheading-color-dark)
	text-transform: uppercase
	letter-spacing: 2px
.has-drop-cap:not(:focus):not(.has-drop-cap_split)
	&:first-letter
		@include dropcap
		&:after
			content: ''
			display: table
			clear: both
.split-text:not(.js-split-text)
	.has-drop-cap
		> div:first-child
			display: inline-block !important
			float: left
			&:after
				content: ''
				display: table
				clear: both
		&:first-letter
			margin: unset
			float: unset
			font-size: unset
			text-transform: unset
.drop-cap
	@include dropcap


.split-text *:not(h1,h2,h3,h4,h5,h6,p,ul,li,div,blockquote,.social__item), .split-text p a
	display: inline-block
	// vertical-align: middle
.split-text__word
	max-width: 100%

code, kbd, tt, var, samp
	font-family: Inconsolata, monospace
pre
	font-family: Inconsolata, monospace
	font-size: 1rem
	line-height: 1.3125
	margin: 0 0 1.75em
	max-width: 100%
	overflow: auto
	padding: 1.5em
	white-space: pre-wrap
	word-wrap: break-word
	background-color: #f8f8f8
code, kbd
	background-color: #f8f8f8
	padding: 0.125em 0.25em

@media screen and (max-width: $sm)
	blockquote, .blockquote
		@include fluid-type(var(--paragraph-min-font-size), var(--paragraph-max-font-size))
		line-height: 1.8
		p
			@include fluid-type(var(--paragraph-min-font-size), var(--paragraph-max-font-size))
			line-height: 1.8
