/* ======================================================================== */
/* 48. postsNavigation */
/* ======================================================================== */
.posts-navigation
  border-top: 1px solid $borders-dark
.posts-navigation__item_prev
  text-align: left
.posts-navigation__item_next
  text-align: right

@media screen and (max-width: $md)
  .posts-navigation__item_prev, .posts-navigation__item_next
    text-align: center
