/* ======================================================================== */
/* 65. tags */
/* ======================================================================== */
.tagcloud, .widget .tagcloud
	a
		display: inline-block
		font-size: 12px !important
		font-weight: 700
		letter-spacing: 1px
		line-height: 1
		text-transform: uppercase
		background-color: transparent
		padding: 8px 14px
		margin-bottom: 6px
		margin-right: 4px
		border-radius: 100px
		border: 1px solid $borders-dark
		&:hover
			border-color: $black
	ul
		+reset-ul
		li
			display: inline-block
			margin-bottom: 0
