/* ======================================================================== */
/* 14. figureIcon */
/* ======================================================================== */
.figure-icon
	text-align: center
.figure-icon__wrapper-icon
	display: inline-flex
	align-items: center
	justify-content: center
	@include fluid('width', 100, 180)
	@include fluid('height', 100, 180)
	border-radius: 50%
	border: 1px solid $borders-dark
	transition: border-color 0.3s ease
	&:hover
		border-color: $black
.figure-icon__wrapper-icon_no-border
	border: none
.figure-icon__header
	display: block
.figure-feature__heading
	display: block

.figure-icon .figure-icon__icon
	@include fluid-type(30, 40)
