/* ======================================================================== */
/* 47. postMeta */
/* ======================================================================== */
.post-meta
	+reset-ul
	li
		display: inline-flex
		flex-wrap: wrap
		// align-items: center
		line-height: 1.6
	li:not(:last-child):after
		content: '/'
		color: $black
		display: inline-block
		margin-left: 6px
		margin-right: 5px
		transition: opacity 0.6s ease
	ul
		padding-left: 0
