/* ======================================================================== */
/* 10. buttonSolidThemes */
/* ======================================================================== */
[data-arts-theme-text="light"], .arts-elementor-theme-light
	.button.button_solid
		&.bg-dark-1
			background-color: #fff !important
			color: var(--color-dark-1)
		&.bg-dark-2
			background-color: #fff !important
			color: var(--color-dark-1)
		&.bg-dark-3
			background-color: #fff !important
			color: var(--color-dark-1)
		&.bg-dark-4
			background-color: #fff !important
			color: var(--color-dark-1)
