/* ======================================================================== */
/* 19. figureProject */
/* ======================================================================== */
.figure-project
	display: block
.figure-project:not(.figure-project_no-hover)
	&:hover:not(:focus)
		.figure-project__content
			transform: translateY(-15px)
.figure-project__link
	position: relative
	display: block
	z-index: 50
.figure-project__heading
	position: relative
	margin-top: 0
	margin-bottom: 0
	color: $black
	z-index: 50
.figure-project__category
	line-height: 1
.figure-project__content
	transition: transform 0.3s ease
	&.text-right, &.text-lg-right
		padding-right: calc(var(--gutter-horizontal) - 20px)
	&.text-left, &.text-lg-left
		padding-left: calc(var(--gutter-horizontal) - 20px)
.figure-project__letter
	display: inline-block
	line-height: 1
	@include fluid-type(67, 400)
	color: $black
	opacity: .05
	white-space: nowrap
.figure-project__wrapper-img
	position: relative
.figure-project__wrapper-letter
	display: inline-block
	position: absolute
	bottom: 0
	left: 0
	transform: translate(-50%, 50%)
	z-index: -1
.figure-project__wrapper-letter_left
	left: 0
	right: auto
.figure-project__wrapper-letter_right
	left: auto
	right: 0
	transform: translate(50%, 50%)
.figure-project__content_absolute
	position: absolute
	left: 0
	right: 0
	bottom: 0
	@include fluid('padding', 30, 60)
	display: flex
	flex-direction: column
	justify-content: space-between
	color: #fff
	z-index: 50
	+trans1
	transform: none !important
.figure-project__overlay-top
	position: absolute
	top: 0
	left: 0
	right: 0
.figure-project__category_absolute
	position: absolute
	top: 0
	left: 0
	right: 0
	@include fluid('padding', 30, 60)
	margin-top: 0
	+trans1
.figure-project__wrapper-content
	position: absolute
	top: 0
	left: 0
	width: 100%
	height: 100%
	z-index: 50
	opacity: 0
	visibility: hidden
	+trans2
	transition-delay: 0s
.figure-project_hover-inner
	position: relative
	&:hover
		.figure-project__overlay
			opacity: 1
			visibility: visible
		.figure-project__wrapper-content
			opacity: 1
			visibility: visible
	.figure-project__overlay
		z-index: 10
		opacity: 0
		visibility: hidden
		transform: scale(1.1)
		+trans2
.figure-project__wrapper-img.pointer-events-auto
	.figure-project__wrapper-letter, .figure-project__letter
		pointer-events: none !important
